body,
main,
article,
section,
aside,
div {
	padding: 20px;
	border: 1px solid #999;
}
main {
	overflow: hidden;
	// !importent
	min-width: 600px;
}
main,
div {
	background: #eee;
}
article,
section,
aside {
	background: #fff;
}
div {
	width: 80px;
	height: 80px;
}
article {
	height: 400px;
	margin: 0 180px 0 260px;
}
aside {
	width: 200px;
	float: left;
}
section {
	width: 120px;
	float: right;
}
